@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  position: relative;
  background-color: rgb(216, 216, 216);
}

.App {
  width: 70vw;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  background-color: #364968;
  color: #fddf97;
  position: absolute;
  margin: 1rem;
  border-radius: 1.5rem;
  top: 50vh;
  left: 50vw;
  -webkit-transform: translate(-50%, -54%);
          transform: translate(-50%, -54%);
}

#input {
  position: relative;
  max-width: 1168px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2rem;
}

form > * {
  margin-bottom: 1.3rem;
}
label {
  margin-top: 1rem;
}
input[type='text'] {
  width: 25%;
  text-align: center;
  border: 1px solid black;
  outline: none;
  border-radius: 0.3rem;
  background-color: #6c4343;
  color: #fddf97;
  height: 1.7rem;
  font-size: 1.1rem;
  font-family: inherit;
}

input[type='text']::-webkit-input-placeholder {
  color: #fddf97;
}

input[type='text']:-ms-input-placeholder {
  color: #fddf97;
}

input[type='text']::placeholder {
  color: #fddf97;
}

input[type='submit'] {
  font-size: 1rem;
  width: 10rem;
  height: 2rem;
  cursor: pointer;
  background-color: #6c4343;
  outline: none;
  border: none;
  border-radius: 0.3rem;
  color: #fddf97;
  font-family: inherit;
  letter-spacing: 0.13rem;
}

input[type='submit']:hover {
  background-color: #fddf97;
  color: #6c4343;
}

.holder {
  display: flex;
  flex-direction: column;
  border-top: 1px black solid;

  padding-bottom: 1rem;
}
h4 {
  letter-spacing: 0.22rem;
  font-size: 1.4rem;
}
.information-box {
  display: flex;
  justify-content: center;
  align-items: center;
}
.information-box > * {
  margin: 1rem;
  border: 1px black solid;
  padding: 1rem;
  border-radius: 1.5rem;
  background-color: #6c4343;
}

.country-display {
  font-size: 2.5rem;
  margin: 0.6rem;
}

.information-box > h4 {
  letter-spacing: 15rem;
}

